.Container {
    height: 16rem;
    width: 3rem;
}

.email {
    cursor: pointer;
    transition: 0.5s;
}

.email:hover {
    font-size: 16px;
    color: aliceblue;
}

.line {
    background-color: #8892B0;
    height: 7rem;
    width: 2px; 
}

@media screen and (max-width: 768px) {
    
    .Container {
        position: relative;
        margin: 5rem 0 5px 0;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 2rem;
        flex-direction: row;
    }

    .line {
        display: none;
    }

    .email {
        font-size: 18px;
    }

    .email:hover {
        font-size: 20px;
    }

}